import React, { useState, useRef, useEffect, useLayoutEffect, useCallback } from 'react';
import { useContentStyle, Controller, ShadowRoot } from '@ali/4ever-cangjie';
import { getSectPr, theme } from '@ali/4ever-utils';
import { debounce } from 'lodash-es';

const { defaultKeyTypographyStyle } = theme;
const THROTTLE_TIME_RESIZE = 200;

const probeStyle: React.CSSProperties = {
  ...defaultKeyTypographyStyle,
  // 绝对布局 + 100%，限制只在 data-cangjie-content 区域变化时才触发变化
  position: 'absolute',
  zIndex: -1,
  width: '100%',
  visibility: 'hidden',
  pointerEvents: 'none',
  top: 0,
  left: 0,
};

const Probe = (props: {
  onRef: (node: HTMLDivElement | null) => void;
  controller: Controller;
}) => {
  const { controller, onRef } = props;
  // 探测需要响应全局样式
  const globleStyle = useContentStyle(controller);

  /**
   * 使用 shadow dom：
   * 1. 避免在正文内插入节点导致大量单测受影响（影响单测中 content.text）
   * 2. 隔离样式，避免性能问题
   */
  return (
    <ShadowRoot>
      <div
        ref={onRef}
        style={{
          ...probeStyle,
          ...globleStyle,
        }}
      >中
      </div>
    </ShadowRoot>
  );
};

interface ProbeProviderProps {
  children: () => React.ReactElement;
  controller: Controller;
}

export const ProbeProvider = (props: ProbeProviderProps) => {
  const [needUpdate, update] = useState<boolean | undefined>(undefined);
  const probeRef = useRef<HTMLDivElement>();
  const obRef = useRef<ResizeObserver | null>();
  const updateRef = useRef<boolean | undefined>(undefined);
  const { controller } = props;
  const pgMar = getSectPr(controller.value.document.data, 'pgMar');
  const left = Math.round(pgMar.left || 0);
  const right = Math.round(pgMar.right || 0);

  const handleResize = useCallback(debounce(() => {
    update(!updateRef.current);
    updateRef.current = !updateRef.current;
  }, THROTTLE_TIME_RESIZE, { leading: true }), []);

  // TODO: 因为表格里可能会 puring（在第一次 render 后再 display none）
  // 在 display:none 的情况触发重算会导致背景消失
  // 这里 hack 仅确保第一次渲染不出问题
  useEffect(() => {
    // 响应页边距 left 和 right
    if (updateRef.current !== undefined) handleResize();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [handleResize, left, right]);

  const onRef = useCallback((node: HTMLDivElement | null) => {
    if (node && window.ResizeObserver) {
      probeRef.current = node;
      obRef.current = new window.ResizeObserver(handleResize);
      obRef.current.observe(node);
    }
    return undefined;
  }, [handleResize]);

  useEffect(() => {
    return () => {
      handleResize.cancel();
      if (obRef.current && probeRef.current) {
        obRef.current.unobserve(probeRef.current);
      }
    };
  }, [handleResize]);

  return (
    <MeasureProbeContext.Provider value={needUpdate}>
      {/* PERF: 使用 props.children 避免 next 在 hook 变更时 rerender */}
      {props.children}
      <Probe onRef={onRef} controller={controller} />
    </MeasureProbeContext.Provider>
  );
};

/**
 * 探测编辑区域 resize
 * 原因可能是窗口 resize 或 默认字号变化等原因
 */
export const MeasureProbeContext = React.createContext<boolean | undefined>(undefined);

/**
 * 返回探测结果
 * Boolean 值标识是否改变
 * @returns Boolean
 */
export const useMeasureProbe = () => {
  return React.useContext(MeasureProbeContext);
};
